<template>
  <div class="selectBlock" :class="{'selectBlock-open':open}">
    <div class="selectBlock__content">
      <label class="selectBlock__placeholder" :class="{'selectBlock__placeholder-value':checkValue}">{{placeholder}}</label>
      <img class="selectBlock__icon" :class="{'selectBlock__icon-open':open}" @click="open = !open" src="@/assets/icon/arrow-down.svg"/>
      <div class="selectBlock__value">
        <div v-if="!multiSelect">{{value[valueText]}}</div>
        <div class="selectBlock__item" v-else v-for="item in value" :key="item[valueId]">{{item[valueText]}}</div>
        <div style="clear: both"></div>
      </div>
    </div>
    <div class="selectBlock__list" :class="{'selectBlock__list-search' : searchInput}" v-show="open">
      <item-input nameField="selectSearch" v-model="search" v-show="searchInput" placeholder="Поиск" icon="icon_search" class="selectBlock__search"></item-input>
      <list-items v-if="open" :items="listItems" :onClick="changeItem"></list-items>
    </div>
  </div>
</template>

<script>
  import ItemInput from './ItemInput'
  import ListItems from '../template/ListItems'

  import mixinEventClick from '@/mixins/eventClick'

  export default {
    name: 'item-select',
    data () {
      return {
        search: ''
      }
    },
    mixins:[
      mixinEventClick
    ],
    props:{
      value:{
        type: [Array, Object],
        required: true
      },
      multiSelect:{
        type: Boolean,
        default: false
      },
      placeholder:{
        type: [String, Boolean],
        default: false
      },
      icon:{
        type: [String, Boolean],
        default: false
      },
      list:{
        type:Array,
        default: []
      },
      searchInput:{
        type: Boolean,
        default: false
      },
      valueId:{
        type: String,
        default: 'id'
      },
      valueText:{
        type: String,
        default: 'value'
      }
    },
    components: {
      ItemInput,
      ListItems
    },
    computed:{
      checkValue(){
        return (Array.isArray(this.value)) ? this.value.length !== 0 : Object.keys(this.value).length !== 0
      },
      listItems(){
        let Items = this.list;
        if(this.search){
          Items = Items.filter((item) =>
            item[this.valueText].toLowerCase().indexOf(this.search.toLowerCase()) + 1);
        }
        return Items;
      }
    },
    methods:{
      changeItem(item){
        if(this.multiSelect){
          let listVal = this.value;
          let IndexEl = listVal.indexOf(item);
          if(IndexEl === -1){
            listVal = [...listVal, item];
          }else{
            listVal.splice(IndexEl, 1);
          }
          this.$emit('input', listVal)
        }
        else
          this.$emit('input', item)
      }
    }
  }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">

  @import "../assets/style/style";
  .selectBlock{
    box-sizing: border-box;
    position: relative;
    min-height: 56px;
    border: 2px solid #e6eaf0;
    padding: 16px 40px 16px 16px;
    border-radius: 2px;
    width: 100%;
    &-open{
      border-color: #498dde;
      border-bottom: 0;
    }
    &__content{
      min-height: 24px;
    }
    &__placeholder{
      cursor: inherit;
      left: 16px;
      top:16px;
      height: 24px;
      line-height: 24px;
      position: absolute;
      font-size: 18px;
      color: #8b98a7;
      font-family: $font-family-regular;
      z-index: 10;
      &-value{
        font-size: 12px;
        top:11px;
        height: 16px;
        line-height: 16px;
      }
    }
    &__value{
      position: relative;
      left: 0;
      top:12px;
      text-align: left;
      width: 100%;
      font-size: 18px;
      min-height: 24px;
      line-height: 24px;
      font-family: $font-family-regular;
    }
    &__item{
      font-family: $font-family-regular;
      display: inline-block;
      background-color: #b8bdce;
      margin-right: 5px;
      font-size: 14px;
      padding: 0 14px;
      border-radius: 15px;
    }
    &__icon{
      position: absolute;
      right: 16px;
      cursor: pointer;
      height: 24px;
      width: 24px;
      &-open{
        transform: rotate(180deg);
      }
    }
    &__list{
      box-sizing: border-box;
      position: absolute;
      bottom: -149px;
      left: -2px;
      width: calc(100% + 4px);
      border: 2px solid #498dde;
      border-top: 0;
      height: 150px;
      overflow: hidden;
      z-index: 11;
      background-color: #fff;
      &-search{
        height: 206px;
        bottom: -206px;
        .list__item:first-of-type{
          border-top: none;
        }
      }
    }
    &__search{
      margin: 0 8px;
      width: calc(100% - 16px);
    }
  }
</style>



