<template>
  <div id="app">
    <item-input :nameField="{attr: 'email', lang: 'E-mail'}" :validateField="valid" v-model="msg" placeholder="E-mail" icon="icon_search" style="margin-bottom: 16px" ></item-input>
    <item-input :nameField="'inn'" v-model="msg1" placeholder="Инн" :info="textInfo" style="margin-bottom: 16px" ></item-input>
    <item-select v-model="obj" placeholder="Подпись" :list="list" :searchInput="true" style="margin-bottom: 16px"></item-select>
    <item-select v-model="arr" :multiSelect="true" placeholder="Множественный выбор" :list="list" style="margin-bottom: 16px"></item-select>
    <item-autocomplete v-model="auto" :fetchSearch="autocomplete" placeholder="Поиск по запросу" style="margin-bottom: 16px"></item-autocomplete>
    <div style="margin-bottom: 16px">
      <item-checkbox v-model="checkBoxFalse" label="label1"/>
      <item-checkbox v-model="checkBoxTrue" label="label2" disabled/>
      <item-checkbox v-model="checkBoxFalse" label="label3" disabled/>
    </div>
    <item-checkbox-group v-model="checkboxGroup" style="margin-bottom: 16px">
        <item-checkbox label="label1"/>
        <item-checkbox label="label2"/>
        <item-checkbox label="label3"/>
    </item-checkbox-group>
    <div style="margin-bottom: 16px">
      <item-radio v-model="radio" label="label1"/>
      <item-radio v-model="radio" label="label2"/>
      <item-radio v-model="radio" label="label3" disabled/>
    </div>
    <item-radio-group v-model="radioGroup" style="margin-bottom: 16px">
      <item-radio label="label1"/>
      <item-radio label="label2"/>
      <item-radio label="label3"/>
    </item-radio-group>
    <div style="margin-bottom: 16px">
      <item-switch v-model="switchValue" label="label1"/>
      <item-switch v-model="switchValue" label="label2" disabled/>
    </div>
  </div>
</template>

<script>
import ItemInput from './components/ItemInput'
import ItemSelect from './components/ItemSelect'
import ItemAutocomplete from './components/ItemAutocomplete'
import ItemCheckbox from './components/ItemCheckbox'
import ItemCheckboxGroup from './components/ItemCheckboxGroup'
import ItemRadio from './components/ItemRadio'
import ItemRadioGroup from './components/ItemRadioGroup'
import ItemSwitch from './components/ItemSwitch'

export default {
  name: 'App',
  data () {
    return {
      textInfo: 'Идентификационный номер налогоплательщика из 12 знаков',
      msg: '',
      msg1: '',
      auto: '',
      obj: {},
      arr: [],
      list:[
        {
          id: 1,
          value: 'Текст1'
        },
        {
          id: 2,
          value: 'Текст21'
        },
        {
          id: 3,
          value: 'Текст31'
        },
        {
          id: 4,
          value: 'Текст41'
        },
        {
          id: 5,
          value: 'Текст51'
        },
        {
          id: 6,
          value: 'Текст61'
        },
      ],
      valid:{
        required: true,
        email: true
      },
      checkBoxTrue: true,
      checkBoxFalse: false,
      checkboxGroup: [],
      radio: 'label3',
      radioGroup: 'label2',
      switchValue: false
    }
  },
  components: {
    ItemInput,
    ItemSelect,
    ItemAutocomplete,
    ItemCheckbox,
    ItemCheckboxGroup,
    ItemRadio,
    ItemRadioGroup,
    ItemSwitch
  },
  methods:{
    autocomplete(query, cb){
      let Items = this.list;
      if(query){
        Items = Items.filter((item) =>
          item.value.toLowerCase().indexOf(query) + 1);
      }
      setTimeout(() => {cb(Items);}, 1000)

    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
  width: 400px;
}
</style>



