<template>
  <div class="list">
    <div ref="list" class="list__items">
      <div class="list__item" v-for="item in listItems" :key="item[valueId]" @click="onClick(item)">
        <item-checkbox v-if="isMultiSelect" :value="isParentValue(item)"/> {{item[valueText]}}
      </div>
    </div>
    <div ref="scroll" class="list__scroll"></div>
  </div>
</template>
<script>
  import mixinScroll from '@/mixins/scroll'
  import ItemCheckbox from '../components/ItemCheckbox'
  export default {
    name: 'list-items',
    created(){
      this.scrollSet();
    },
    mixins:[mixinScroll],
    components:{ItemCheckbox},
    props:{
      items:{
        type: Array,
        default: [],
        required: true
      },
      onClick:{
        type: Function,
        required: true
      },
      valueId:{
        type: String,
        default: 'id'
      },
      valueText:{
        type: String,
        default: 'value'
      }
    },
    computed:{
      listItems(){
        if(this.items.length)
          this.scrollSet();
        return this.items;
      },
      isMultiSelect(){
        return this.$parent.multiSelect;
      },
    },
    methods:{
      isParentValue(item){
        return this.$parent.value.some((el) => el[this.valueId] === item[this.valueId]);
      }
    }
  }
</script>
<style lang="scss">
  @import "../assets/style/style";
  .list{
    box-sizing: border-box;
    position: relative;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 150px;
    overflow: hidden;
    padding: 0 8px;
    z-index: 11;
    background-color: #fff;
    &__items{
      position: absolute;
      right: -30px;
      left: 8px;
      padding: 0 30px 0 0;
      width: 100%;
      height: 144px;
      overflow-y: scroll;
    }
    &__item{
      box-sizing: border-box;
      border-top: 2px solid #e6eaf0;
      height: 48px;
      text-align: left;
      font-family: $font-family-regular;
      font-size: 18px;
      line-height: 48px;
      cursor: pointer;
      padding: 0 8px;
      &:hover{
        background-color: rgba(73, 141, 222, 0.05);
        color: #498dde;
      }
    }
    &__scroll{
      position: absolute;
      background-color: #e6eaf0;
      border-radius: 3px;
      width: 6px;
      height: 10px;
      right: 0;
      top: 0;
    }
  }
</style>



