export default {
  data () {
    return {
      open: false
    }
  },
  watch:{
    open: function (val) {
      if(val){
        this.missClick();
      }else{
        this.missClickClear();
      }
    }
  },
  methods: {
    missClick(){
      window.addEventListener("click", this.clickBlock);
    },
    missClickClear(){
      window.removeEventListener("click", this.clickBlock)
    },
    clickBlock(event){
      const listClass =['selectBlock', 'autocomplete'];
      let clickElement = event.path.some((el) => this.findClass(listClass, el.className))
      if(!clickElement){
        this.open = false;
      }
    },
    findClass (arr, substring) {
      if(substring === undefined) return false;
      let strFind = substring.split(' ');
      if(strFind[0].length){
        let str = arr.join();
        let res = str.search(strFind[0]);
        if(res !== -1){
          return true
        }
      }
      return false;
    }
  }
};



