export default {
  methods: {
    scrollSet(){
      this.$nextTick(() => {
        let contentHeight = this.items.length * 48;
        let scrollHeight = Math.min(((this.searchInput) ? 194: 160) / contentHeight) * 100;
        if(contentHeight > 160){
          this.$refs.scroll.style.height = scrollHeight + 'px';
          this.$refs.list.onscroll = (evt) => {
            this.$refs.scroll.style.top = evt.target.scrollTop / (contentHeight - scrollHeight) * 100  + '%'
          }
        }else{
          this.$refs.scroll.style.height = '0px';
        }
      })
    },
    scrollClear(){
      this.$refs.items.onscroll = null;
    }
  }
};



