<template>
  <label
    class="checkbox"
    role="checkbox"
    :aria-checked="checked"
    :aria-disabled="disabled"
    :id="id"
    :class="[
      { 'checkbox--disabled': isDisabled },
      { 'checkbox--focus': focus }
    ]"
  >
    <span class="checkbox__input">
      <span
        class="checkbox__inner"
        :class="{
          'checkbox__inner--disabled': isDisabled,
          'checkbox__inner--checked': isChecked
        }"></span>
      <input
        class="checkbox__initial"
        type="checkbox"
        aria-hidden="true"
        :disabled="isDisabled"
        :value="label"
        :name="name"
        v-model="model"
        @change="handleChange"
        @focus="focus = true"
        @blur="focus = false">
    </span><span class="checkbox__label" v-if="$slots.default || label">
      <slot></slot>
      <template v-if="!$slots.default">{{label}}</template>
    </span>
  </label>
</template>

<script>
export default {
  props: {
    value: Boolean,
    disabled: Boolean,
    checked: Boolean,
    name: String,
    label: {},
    id: String
  },
  data () {
    return {
      selfModel: false,
      focus: false
    }
  },
  computed: {
    model: {
      get () {
        return this.isGroup
          ? this.store : this.value !== undefined
            ? this.value : this.selfModel;
      },
      set (val) {
        if (this.isGroup) {
          this.dispatch('ItemCheckboxGroup', 'input', [val]);
        } else {
          this.$emit('input', val);
          this.selfModel = val;
        }
      }
    },
    isChecked() {
      if ({}.toString.call(this.model) === '[object Boolean]') {
        return this.model;
      } else if (Array.isArray(this.model)) {
        return this.model.indexOf(this.label) > -1;
      } else if (this.model !== null && this.model !== undefined) {
        return this.model === this.label;
      }
    },
    isDisabled () {
      return this.isGroup
        ? this._checkboxGroup.disabled || this.disabled
        : this.disabled;
    },
    isGroup () {
      let parent = this.$parent;
      while (parent) {
        if (parent.$options.name !== 'ItemCheckboxGroup') {
          parent = parent.$parent;
        } else {
          this._checkboxGroup = parent;
          return true;
        }
      }
      return false;
    },
    store() {
      return this._checkboxGroup ? this._checkboxGroup.value : this.value;
    }
  },
  methods: {
    addToStore() {
      if (Array.isArray(this.model) && this.model.indexOf(this.label) === -1) {
        this.model.push(this.label);
      } else {
        this.model = true;
      }
    },
    handleChange(e) {
      let value = !!e.target.checked;
      this.$emit('change', value, e);
      this.$nextTick(() => {
        if (this.isGroup) {
          this.dispatch('ItemCheckboxGroup', 'change', [this._checkboxGroup.value]);
        }
      });
    },
    dispatch(componentName, eventName, params) {
      let parent = this.$parent || this.$root;
      let name = parent.$options.name;
      while (parent && (!name || name !== componentName)) {
        parent = parent.$parent;

        if (parent) {
          name = parent.$options.name;
        }
      }
      if (parent) {
        parent.$emit.apply(parent, [eventName].concat(params));
      }
    }
  },
  created() {
    this.checked && this.addToStore();
  }
}
</script>

<style lang="scss">
  .checkbox {
    color: #8b98a7;
    position: relative;
    cursor: pointer;
    display: inline-block;
    white-space: nowrap;
    user-select: none;

    &+& {
      margin-left: 30px;
    }

    &__input {
      white-space: nowrap;
      cursor: pointer;
      outline: none;
      display: inline-block;
      padding: 2px;
      width: 20px;
      height: 20px;
      margin: 0;
      line-height: 1;
      position: relative;
      vertical-align: middle;
    }

    &__inner {
      display: block;
      height: 100%;
      position: relative;
      border: 2px solid #B8BDCE;
      border-radius: 2px;
      box-sizing: border-box;
      background-color: #fff;
      z-index: 1;
      &:not(.checkbox__inner--disabled):hover {
        border-color: #498DDE;
      }

      &:after {
        box-sizing: content-box;
        border: 2px solid #fff;
        border-left: 0;
        border-top: 0;
        height: 9px;
        width: 4px;
        left: 5px;
        position: absolute;
        top: 1px;
        transform: rotate(45deg);
      }

      &--checked {
        border-color: #498DDE;
        background-color: #498DDE;
        &:after {
          content: '';
        }
      }

      &--disabled {
        border-color: #E6EAF0;
        background-color: #EEF1F3;
      }
    }
    &__initial {
      opacity: 0;
      outline: none;
      position: absolute;
      z-index: -1;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      margin: 0;
    }
    &__label {
      display: inline-block;
      vertical-align: middle;
      padding-left: 12px;
      line-height: 1.25;
      font-size: 16px;
    }
    &--disabled &__input, &--disabled &__label {
      cursor: not-allowed;
    }
    &--focus:not(&--disabled) &__inner {
      border-color: #498DDE;
    }
  }
</style>



