<template>
  <div class="autocomplete">
    <item-input nameField="autocomplete" v-model="query" :placeholder="placeholder" class="autocomplete__input" :class="{'autocomplete__input-value': checkOpen}"></item-input>
    <div class="autocomplete__list" v-show="checkOpen">
      <i class="autocomplete__load icon_load" v-if="load"></i>
      <list-items v-if="checkOpen" :items="listItems" :onClick="getValue"></list-items>
    </div>
  </div>
</template>

<script>
  import ItemInput from './ItemInput'
  import ListItems from '../template/ListItems'

  import mixinEventClick from '@/mixins/eventClick'

  export default {
    name: 'item-autocomplete',
    data(){
      return {
        listItems: [],
        load: false,
        clickList: false
      }
    },
    mixins:[
      mixinEventClick
    ],
    components: {
      ItemInput,
      ListItems
    },
    props:{
      value:{
        type: String,
        required: true
      },
      placeholder:{
        type: [String, Boolean],
        default: false
      },
      fetchSearch:{
        type: Function,
        required:true
      },
      valueId:{
        type: String,
        default: 'id'
      },
      valueText:{
        type: String,
        default: 'value'
      }
    },
    computed:{
      query:{
        get(){
          return this.value;
        },
        set(val){
          if(val.length && !this.clickList){
            this.listItems = [];
            this.load = true;
            this.open = true;
            this.missClick();
            this.getList(val, this.itemList);
          }
          else{
            this.missClickClear();
            this.clickList = false;
            this.open = false;
            this.listItems = [];
          }

          this.$emit('input', val)
        }
      },
      getList(){
        return this.fetchSearch;
      },
      checkOpen(){
        return (this.load || this.listItems.length) && this.open;
      }
    },
    methods:{
      itemList(list){
        this.load = false;
        this.listItems = list;
      },
      getValue(item){
        this.clickList = true;
        this.query = item[this.valueText]
      }
    }
  }
</script>

<style lang="scss">
  @import "../assets/style/style";
  .autocomplete{
    box-sizing: border-box;
    position: relative;
    height: 56px;
    width: 100%;
    border-radius: 2px;
    &__input{
      &-value{
        border-bottom: none;
        border-color: #498dde;
      }
    }
    &__list{
      box-sizing: border-box;
      position: absolute;
      top: 53px;
      left: 0;
      width: 100%;
      border: 2px solid #498dde;
      border-top: 0;
      height: 150px;
      overflow: hidden;
      z-index: 11;
      background-color: #fff;
    }
    &__load{
      margin-top: 49px;
    }
  }
</style>



