<template>
  <label
    class="radio"
    role="radio"
    :aria-checked="model === label"
    :aria-disabled="isDisabled"
    :tabindex="tabIndex"
    @keydown.space.stop.prevent="model = isDisabled ? model : label"
    :class="[
      { 'radio--disabled': isDisabled },
      { 'radio--focus': focus }
    ]"
  >
    <span class="radio__input">
      <span
        class="radio__inner"
        :class="{
          'radio__inner--disabled': isDisabled,
          'radio__inner--checked': model === label
        }"
      ></span>
      <input
        class="radio__initial"
        :value="label"
        type="radio"
        aria-hidden="true"
        v-model="model"
        @focus="focus = true"
        @blur="focus = false"
        @change="handleChange"
        :name="name"
        :disabled="isDisabled"
        tabindex="-1"
      >
    </span><span class="radio__label" @keydown.stop>
      <slot></slot>
      <template v-if="!$slots.default">{{label}}</template>
    </span>
  </label>
</template>
<script>
  export default {
    name: 'ItemRadio',
    props: {
      value: {},
      label: {},
      disabled: Boolean,
      name: String
    },
    data () {
      return {
        focus: false
      };
    },
    computed: {
      isGroup() {
        let parent = this.$parent;
        while (parent) {
          if (parent.$options.name !== 'ItemRadioGroup') {
            parent = parent.$parent;
          } else {
            this._radioGroup = parent;
            return true;
          }
        }
        return false;
      },
      model: {
        get() {
          return this.isGroup ? this._radioGroup.value : this.value;
        },
        set(val) {
          if (this.isGroup) {
            this.dispatch('ItemRadioGroup', 'input', [val]);
          } else {
            this.$emit('input', val);
          }
        }
      },
      isDisabled() {
        return this.isGroup
          ? this._radioGroup.disabled || this.disabled
          : this.disabled;
      },
      tabIndex() {
        // return !this.isDisabled ? (this.isGroup ? (this.model === this.label ? 0 : -1) : 0) : -1;
        return (this.isDisabled && -1) || (!this.isGroup && '0') || (this.model === this.label ? 0 : -1);
      }
    },
    methods: {
      handleChange() {
        this.$nextTick(() => {
          this.$emit('change', this.model);
          this.isGroup && this.dispatch('ItemRadioGroup', 'handleChange', this.model);
        });
      },
      dispatch(componentName, eventName, params) {
        let parent = this.$parent || this.$root;
        let name = parent.$options.name;

        while (parent && (!name || name !== componentName)) {
          parent = parent.$parent;

          if (parent) {
            name = parent.$options.name;
          }
        }
        if (parent) {
          parent.$emit.apply(parent, [eventName].concat(params));
        }
      }
    }
  };
</script>

<style lang="scss">
  .radio {
    color: #8b98a7;
    position: relative;
    cursor: pointer;
    display: inline-block;
    white-space: nowrap;
    user-select: none;
    outline: none;
    &+& {
      margin-left: 30px;
    }

    &__input {
      white-space: nowrap;
      cursor: pointer;
      outline: none;
      display: inline-block;
      padding: 2px;
      width: 20px;
      height: 20px;
      margin: 0;
      line-height: 1;
      position: relative;
      vertical-align: middle;
    }

    &__inner {
      display: block;
      height: 100%;
      position: relative;
      border: 2px solid #B8BDCE;
      border-radius: 50%;
      box-sizing: border-box;
      z-index: 1;
      &:not(.radio__inner--disabled):hover {
        border-color: #498DDE;
      }
      &--checked {
        border-color: #498DDE;
        &:after {
          content: '';
        } 
      }

      &:after {
        box-sizing: content-box;
        border-radius: 50%;
        background-color: #498DDE;
        height: 10px;
        width: 10px;
        left: 3px;
        position: absolute;
        top: 3px;
        transform: rotate(45deg);
      }
      &--disabled {
        border-color: #E6EAF0;
        background-color: #EEF1F3;
        &:after {
          background-color: #ffffff;
        }
      }
    }
    &__initial {
      opacity: 0;
      outline: none;
      position: absolute;
      z-index: -1;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      margin: 0;
    }
    &__label {
      display: inline-block;
      vertical-align: middle;
      padding-left: 12px;
      line-height: 1.25;
      font-size: 16px;
    }
    &--disabled &__input, &--disabled &__label {
      cursor: not-allowed;
    }
    &:focus:not(&--focus):not(&--disabled) &__inner {
      border-color: #498DDE;
    }
  }
</style>



