<template>
  <div @click="focusInput()" class="inputBlock" :class="{'inputBlock-active': focus, 'error' : errors.has(nameField.attr)}">

    <input
      v-validate="validateField"
      @focus="focus = true"
      @blur="focus = false"
      v-mask="mask"
      ref="inputText"
      class="inputBlock__input"
      :class="{'inputBlock__input-active': checkSymbol}"
      v-model="string"
      :data-vv-name="nameField.attr || nameField"
    />

    <label @click="focusInput()" class="inputBlock__placeholder" v-if="placeholder">{{placeholder}}</label>
    <i class="inputBlock__icon" :class="[icon]" aria-hidden="true" v-if="checkIcon()"></i>
    <div class="inputBlock__help" :class="{'icon_status_info' : !errors.has(nameField.attr), 'icon_status_error' : errors.has(nameField.attr)}" v-if="info || errors.has(nameField.attr)">
      <div :class="{'error' : errors.has(nameField.attr)}">{{(this.errors.has(nameField.attr)) ? errors.first(nameField.attr) : info}}</div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'item-input',
  mounted() {
    if(typeof this.nameField === 'object')
      this.$validator.localize('ru', {
        attributes: {
          [this.nameField.attr]: this.nameField.lang,
        }
      });
  },
  data(){
    return {
      focus: false,
    }
  },
  props:{
    value:{
      type: String,
      required: true
    },
    nameField:{
      type: [String, Object],
      required: true
    },
    placeholder:{
      type: String,
    },
    icon:{
      type: String,
      default: ''
    },
    mask:{
      type:String,
      default: ''
    },
    info:{
      type:String,
    },
    validateField:{
      type: Object
    },
  },
  computed:{
    checkSymbol(){
      return (this.value.length) && true;
    },
    string:{
      get(){
        return this.value;
      },
      set(val){
        this.$emit('input', val)
      }
    },
  },
  methods:{
    focusInput(){
      this.$refs.inputText.focus()
    },
    checkIcon(){
      if(this.info || this.errors.has(this.nameField.attr)) return false;
      return true;
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">

  @import "../assets/style/style";
  .inputBlock{
    box-sizing: border-box;
    position: relative;
    height: 56px;
    width: 100%;
    border: 2px solid #e6eaf0;
    padding: 16px 40px 16px 16px;
    border-radius: 2px;
    &.error{
      border-color: #c84f4f;
    }
    &-active{
      border-color: #498dde;
    }
    &__input{
      border: none;
      position: absolute;
      left: 16px;
      top:16px;
      width: calc(100% - 56px);
      outline: none;
      box-shadow: none;
      font-size: 18px;
      height: 24px;
      font-family: $font-family-regular;
      &:focus,
      &-active{
        top:25px;
        & ~ .inputBlock__placeholder{
          font-size: 12px;
          top:11px;
          height: 16px;
          line-height: 16px;
        }
      }
    }
    &__placeholder{
      cursor: inherit;
      left: 16px;
      top:16px;
      height: 24px;
      line-height: 24px;
      position: absolute;
      font-size: 18px;
      color: #8b98a7;
      font-family: $font-family-regular;
      z-index: 10;
    }
    &__icon{
      position: absolute;
      right: 16px;
      height: 24px;
      width: 24px;
    }
    &__help{
      position: absolute;
      right: 16px;
      height: 24px;
      width: 24px;
      & > div{
        display: none;
        background-color: #498dde;
        &.error{
          background-color: #c84f4f;
          &:after {
            border-bottom-color: #c84f4f!important;
          }
        }
      }
      &:hover{
        & > div {
          display: block;
          font-family: $font-family-regular;
          font-size: 14px;
          color: #ffffff;
          text-align: left;
          min-width: 200px;
          max-width: 300px;
          position: absolute;
          top: 36px;
          right: -8px;
          z-index: 12;
          padding: 16px;
          &:after {
            content: '';
            position: absolute; /* Абсолютное позиционирование */
            right: 10px; top: -20px; /* Положение треугольника */
            border: 10px solid transparent;
            border-bottom: 10px solid #498dde;
          }
        }
      }
    }
  }
</style>



